import React from 'react'
import styled from 'styled-components'
import { setButtonSyles } from './utils'

const ButtonWrapper = styled.div`
  position: relative;
  display: inline-block;
  // border: 1px solid red;
`

const buttonShadowAnimation = keyframes`
  from {
    top: 0px;
    left: 0px;
    box-shadow: 4px 4px 0px rgba(88, 97, 101, 0.3);
  }

  to {
    top: 4px;
    left: 4px;,
    box-shadow: none;
  }
`

const StyledButton = styled.button`
  box-sizing: border-box;
  position: relative;
  padding: 10px 30px 10px 30px;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  color: white;
  background: red;
  border: 2px solid red;
  box-shadow: 4px 4px 0px rgba(88, 97, 101, 0.3);

  &:hover {
    top: 4px;
    left: 4px;
    box-shadow: none;
    animation: ${buttonShadowAnimation} 125ms ease
  }

  &:active {

  }
`

// const StyledButton = styled.button`
//   background: ${props => props.background};
// `

export default function Button(props) {
  
  const {
    background
  } = setButtonSyles(props.buttonType)

  return (
      <ButtonWrapper>
        <StyledButton
          background={background}
        >{props.label}</StyledButton>
      </ButtonWrapper>
  )
}
